// CAN DELETE AFTER WHEN STARTING A NEW PROJECT
header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2vh 2.5%;
  z-index: 999999;

  a {
    color: $black;
  }

  .site-branding {
    font-size: 1.5rem;
  }

  nav {
    li {
      font-size: 1.5rem;
    }
  }
}

.custom-cursor {
  position: absolute;
  z-index: 99999;
  width: fit-content;
  left: -8%;
  top: -1%;

  span {
    font-size: 6rem;
    font-weight: 700;
    color: $light-grey;
  }
}

#hero {
  height: 100vh;
  width: 100%;
}

.img-container {
  height: 50vh;
  width: 60%;
  position: relative;
  box-shadow: $box-shadow;
  overflow: hidden;

  img {
    position: absolute;
    left: -10%;
    top: -10%;
    object-fit: cover;
    height: 120%;
    width: 120%;
  }
}

// ABOUT
#about {
  min-height: 100vh;
  padding: 10vh 0;
  width: 100%;
  position: relative;

  #about-cards {
    width: 80%;
    margin: 0 auto;

    .card {
      width: 100%;
      padding: 50px;
      box-shadow: $box-shadow;
      border-radius: 5px;
    }
  }
}

.scroll-cta {
  padding-top: 40px;
  text-align: center;
  line-height: 1.4;
  font-size: 3rem;
}

#parallax-pg {
  height: 100vh;
  width: 100%;
  display: grid;
  place-items: center;
}
.container {
  display: flex;
  align-items: center;
  padding: 10vh 0;
  width: 80%;
  margin: 0 auto;

  .box {
    height: 500px;
    width: 60%;
    background: $light-grey;
  }

  p {
    position: relative;
    left: -10%;
    font-size: 2rem;
    line-height: 1.4;
  }
}

#event-group {
  width: 90%;
  margin: 0 auto;
  padding: 10vh 0;

  button {
    width: fit-content;
    margin: 0 auto 50px auto;
    background: none;
    border: none;
    padding: 20px;
    box-shadow: $box-shadow;
  }

  .show-me {
    height: 50vh;
    margin: 0 auto;
    opacity: 0;
    padding: 40px;
    box-shadow: $box-shadow;
    border-radius: 4px;
    transition: all 0.3s ease;

    code {
      font-size: 1.5rem;
      line-height: 1.1;
    }
  }

  .show-me-active {
    opacity: 1;
  }
}

@media (min-width: $breakpoint-small) {
  #event-group {
    width: 60%;
  }

  .show-me {
    code {
      font-size: 2rem;
      line-height: 1.5;
    }
  }
}
