// GRIDS
// Add to the parent of the grid
.auto-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.grid-gap-sm {
  grid-gap: 10px;
}

.grid-gap-md {
  grid-gap: 20px;
}

.grid-gap-lg {
  grid-gap: 40px;
}

// POSITIONS
// Attach these to the parents

// Works well with one child
.center-block {
  display: grid;
  place-items: center;
}

// Works well with multiple children
.center-content {
  display: grid;
  justify-self: center;
  align-self: center;
}

// IMAGES
// Attach to the img
.full-img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}